import React from "react";
import { ThemeProvider } from "styled-components";
import { RawIntlProvider, createIntl } from "react-intl";
import { theme } from "@orthogonalgroup/ui/src/styles/theme";
import { ToastContainer } from "react-toastify";
import type { Preview } from "@storybook/react";

const preview: Preview = {
  parameters: {
    actions: { argTypesRegex: "^on[A-Z].*" },
    controls: {
      matchers: {
        color: /(background|color)$/i,
        date: /Date$/,
      },
    },
  },
};

export default preview;


// mock intl provider
const intlCache = createIntl({
  locale: "en-US",
  onError: (e) => {
    if (
      typeof e?.message === 'string' &&
      e?.message.startsWith('[@formatjs/intl Error MISSING_TRANSLATION] Missing message:')
    ) {
      return;
    }
    throw e;
  },
});

/** @description if there is multiple library & theme, do some test and then provide different theme */
export const decorators = [
  (Story) => (
    <RawIntlProvider value={intlCache}>
      <Story />
    </RawIntlProvider>
  ),
  (Story) => {
    return (
      <ThemeProvider theme={theme}>
        <Story />
        <ToastContainer position="top-center" hideProgressBar style={{ width: 280 }} />
      </ThemeProvider>
    )
  },
];
